color: currentColor\n    border-radius: inherit\n    bottom: 0\n    color: inherit\n    content: ''\n    left: 0\n    opacity: 0\n    pointer-events: none\n    position: absolute\n    right: 0\n    top: 0\n    transition: $btn-transition\n\n  &:not(.v-btn--round)\n    @each $name, $size in $btn-sizes\n      &.v-size--#{$name}\n        height: #{$size}px\n        min-width: #{round($size * 1.777777777777778)}px // default ratio\n        padding: 0 #{$size / 2.25}px\n\n  > .v-btn__content .v-icon\n    color: inherit\n\n// Elements\n.v-btn__content\n  align-items: center\n  color: inherit\n  display: flex\n  // https://github.com/vuetifyjs/vuetify/issues/7580\n  flex: 1 0 auto\n  justify-content: inherit\n  line-height: normal\n  // Fixes bug where IE11 moves\n  // button content when clicked\n  // https://stackoverflow.com/questions/10305658/prevent-button-from-shifting-during-click-in-ie\n  position: relative\n  transition: inherit\n  transition-property: opacity\n\n  // Increased specificity to overwrite in VIcon.sass\n  // iconfont css interference\n  .v-icon.v-icon--left,\n  .v-icon.v-icon--right\n    font-size: $btn-icon-font-size\n    height: $btn-icon-font-size\n    width: $btn-icon-font-size\n\n  .v-icon--left\n    +ltr()\n      margin-left: -4px\n      margin-right: 8px\n\n    +rtl()\n      margin-left: 8px\n      margin-right: -4px\n\n  .v-icon--right\n    +ltr()\n      margin-left: 8px\n      margin-right: -4px\n\n    +rtl()\n      margin-left: -4px\n      margin-right: 8px\n\n.v-btn__loader\n  align-items: center\n  display: flex\n  height: 100%\n  justify-content: center\n  left: 0\n  position: absolute\n  top: 0\n  width: 100%\n\n// Modifiers\n.v-btn--absolute,\n.v-btn--fixed\n  &.v-btn--right\n    right: map-get($grid-gutters, 'lg')\n\n  &.v-btn--left\n    left: map-get($grid-gutters, 'lg')\n\n  &.v-btn--top\n    top: map-get($grid-gutters, 'lg')\n\n  &.v-btn--bottom\n    bottom: map-get($grid-gutters, 'lg')\n\n.v-btn--absolute\n  position: absolute\n\n